<template>
<!--        -->
  <div class="text-center">
    <v-container>
      <v-layout row wrap justify-space-between>
        <!-- Datepicker -->
        <v-flex xs12 md3>
          <v-menu>
              <template v-slot:activator="{ on }">
                <v-text-field :value="initialDate" label="Diary date" prepend-icon="mdi-calendar" v-on="on"></v-text-field>
              </template>
              <v-date-picker 
                v-model="initialDate"
                @change="onChangeDate"
              />
          </v-menu>
        </v-flex>
        <v-flex xs12 md6 v-if="!isEditingEnabled">
          <p >Calories: {{ diet.nutritionalValue.calories }}, proteins: {{ diet.nutritionalValue.proteinsInGrams }}, fats: {{ diet.nutritionalValue.fatsInGrams }}, carbs: {{ diet.nutritionalValue.carbsInGrams }}</p>
        </v-flex>
        <!-- Dialog for adding a meal -->
        <v-dialog v-if="isEditingEnabled"
          v-model="dialogAddMealOpened"
          max-width="400px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-flex xs12 md3>
              <v-btn
                color="primary"
                class="mb-2"
                dark
                v-bind="attrs"
                v-on="on"
                block
              >
                Add meal
              </v-btn>
            </v-flex>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">Add meal</span>
            </v-card-title>
            <v-form ref="formAddMeal">
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-select
                        v-model="mealTypeId"
                        :items="mealTypes"
                        item-value="id"
                        item-title="name"
                        item-text="name"
                        label="Food"
                        return-id
                        required
                        :rules="isNotEmptyRule">
                      </v-select>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-form>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="dialogAddMealOpened = false"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="addMeal"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Toggle edit/save -->
        <v-flex xs12 md3>
          <v-btn v-if="!isEditingEnabled"
            color="primary"
            dark
            class="mb-2"
            @click="isEditingEnabled = true"
            block
          >
            Edit
          </v-btn>
          <v-btn v-else
            color="success"
            dark
            class="mb-2"
            block
            @click="onClickSave"
          >
            Save
          </v-btn>
        </v-flex >
      </v-layout>

      <!-- List of meals -->
      <div :key="rebuildCounter">
        <MealComponent v-for="meal in diet.meals" :key="meal.key"
          :is-editing-enabled="isEditingEnabled"
          :meal-prop="meal"
          :food-list="foodList"
          :meal-types="mealTypes"
          @deleteMeal="onDeleteMeal($event)"
          />
      </div>
      <v-btn
        color="blue darken-1"
        text
        @click="test"
      >
        Test
      </v-btn>
    </v-container>
  </div>
</template>
    
<script>
import MealComponent from '@/components/MealComponent.vue';

export default {
  components: {MealComponent},
  data() {
    return {
      rebuildCounter: 0,
      initialDate: new Date().toJSON().slice(0, 10),
      diet: { meals: [], nutritionalValue: {calories: 0, proteinsInGrams: 0}},
      mealTypes: [],
      foodList: [],
      isEditingEnabled: false,
      dateMenuOpened: false,
      dialogAddMealOpened: false,
      mealTypeId: null,
      isNotEmptyRule: [str => !(str == null || str == undefined || str == '') || "The field is required"],
      mealKey: 1,
    }
  },
  methods: {
    getAllMealTypes:function() {
      this.$http.get(this.$hostname + '/api/v1/meal-types').then(response => {
        this.mealTypes = response.body
      }).catch(e => console.log(e))
    },
    getAllFood:function() {
      this.$http.get(this.$hostname + '/api/v1/food').then(response => {
        this.foodList = response.body
      }).catch(e => console.log(e))
    },
    getDietForDateAndUser:function(onEmptyResponseCallback = () => {}) {
      this.$http.get(this.$hostname + `/api/v1/diet?date=${this.initialDate}&userId=1`).then(response => {
        if(response.body == "") {
          onEmptyResponseCallback()
        }
        else {
          this.diet = response.body
          //https://stackoverflow.com/questions/32106155/can-you-force-vue-js-to-reload-re-render
          this.rebuildCounter++;
        }
      }).catch(e => console.log(e))
    },
    postDiet:function(diet) {
      this.$http.post(this.$hostname + '/api/v1/diet', diet)
        .then(this.getDietForDateAndUser())
        .catch(e => console.log(e))
    },
    getDietForDateAndUserAndPostIfItDoesntExist:function() {
      let newDiet = { id: null, date: this.initialDate, userId: 1, meals: [] }
      let callback = () => this.postDiet(newDiet)
      this.getDietForDateAndUser(callback)
    },
    putDiet:function(diet, onSuccessCallback = () => {}) {
      this.$http.put(this.$hostname + '/api/v1/diet', diet)
        .then(() => {
          onSuccessCallback()
          this.rebuildCounter++;
        })
        .catch(e => console.log(e))
    },

    addMeal:function() {
      if(this.$refs.formAddMeal.validate()) {
        let meal = {
          id: null,
          key:this.mealKey++,
          mealTypeId: this.mealTypeId,
          courses: []
        }
        this.diet.meals.push(meal)
        
        this.mealTypeId = null
        this.dialogAddMealOpened = false
      }
    },
    onDeleteMeal:function(eventObject) {
      let meal = this.findMealByKey(eventObject.key)
      let index = this.diet.meals.indexOf(meal);
      if (index > -1) { // only splice array when item is found
        this.diet.meals.splice(index, 1); // 2nd parameter means remove one item only
      }
    },
    onChangeDate:function() {
      this.isEditingEnabled = false
      this.getDietForDateAndUserAndPostIfItDoesntExist()
    },
    onClickSave() {
      this.isEditingEnabled = false
      this.putDiet(this.diet, this.getDietForDateAndUser)
    },

    findMealByKey:function(key) {
      let result = this.diet.meals.find(elem => elem.key == key)
      return result
    },
    test:function() {
      console.log(this.diet)
    }
  },
  created() {
    this.getAllFood()
    this.getAllMealTypes()
    this.getDietForDateAndUserAndPostIfItDoesntExist()
  }
}
</script>