<template>
  <v-container class="my-10">
    <v-row class="pb-10 px-2">
      <h1 class="subtitle-4 grey--text">Food dictionary</h1>
      <v-spacer></v-spacer>
      <AddFoodForm :food-categories="foodCategories" :nutrient-storage-types="nutrientStorageTypes"/>
    </v-row>
    <v-data-table 
      :headers="headers" 
      :items="foodList" 
      item-key="name" 
      class="elevation-1">
      <template v-slot:body="{ items, headers }">
          <tbody>
            <tr v-for="(item,idx) in items" :key="idx">
                <td v-for="(header,key) in headers" :key="key">
                  <v-select v-if="header.type == 'foodCategorySelect'"
                      v-model="item.foodCategoryId"
                      :items="foodCategories"
                      item-value="id"
                      item-title="name"
                      item-text="name"
                      @change="putFood(item)"
                      return-id>
                  </v-select>
                  <v-select v-else-if="header.type == 'nutrientsStorageTypeSelect'"
                      v-model="item.nutrientsStorageTypeId"
                      :items="nutrientStorageTypes"
                      item-value="id"
                      item-title="name"
                      item-text="name"
                      @change="putFood(item)"
                      return-id>
                  </v-select>
                  <v-icon v-else-if="header.type == 'delete'"
                    small
                    @click="deleteFood(item)"
                  >
                    mdi-delete
                  </v-icon>
                  <v-edit-dialog v-else
                    :return-value.sync="item[header.value]"
                    @save="putFood(item)"
                    large
                  > {{item[header.value]}}
                    <template v-slot:input>
                      <v-text-field
                        v-model="item[header.value]"
                        label="Edit"
                        single-line
                      ></v-text-field>
                    </template>
                  </v-edit-dialog>
                </td>
            </tr>
          </tbody>
      </template>
    </v-data-table>
  </v-container>
</template>


<script>
import AddFoodForm from '@/components/AddFoodForm.vue'

export default {
  components: { AddFoodForm },
  data () {
    return {
      addFoodPath: '/add-food',
      headers: [
        {
          text: "Food",
          align: "left",
          sortable: true,
          value: "name",
          type: "text",
        },
        { text: "Calories", value: "calories", type: "text" },
        { text: "Fat (g)", value: "fatsInGrams", type: "text" },
        { text: "Carbs (g)", value: "carbsInGrams", type: "text" },
        { text: "Protein (g)", value: "proteinsInGrams", type: "text" },
        { text: "Food Type", value: "foodCategoryId", type: "foodCategorySelect" },
        { text: "Data per", value: "nutrientsStorageTypeId", type: "nutrientsStorageTypeSelect" },
        { text: "Actions", value: "id", type: "delete" },
      ],
      foodList: [],
      foodCategories: [],
      nutrientStorageTypes: [],
    }
  },
  methods: {
    getAllFood:function() {
      this.$http.get(this.$hostname + '/api/v1/food').then(response => {
        this.foodList = response.body
      }).catch(e => console.log(e))
    },
    getAllFoodCategories:function() {
      this.$http.get(this.$hostname + '/api/v1/food-categories').then(response => {
        this.foodCategories = response.body
      }).catch(e => console.log(e))
    },
    getNutrientStorageTypes:function() {
      this.$http.get(this.$hostname + '/api/v1/nutrient-storage-types').then(response => {
        this.nutrientStorageTypes = response.body
      }).catch(e => console.log(e))
    },
    putFood:function(food) {
      this.$http.put(this.$hostname + '/api/v1/food', food).catch(e => console.log(e))
    },
    deleteFood:function(food) {
      this.$http.delete(this.$hostname + '/api/v1/food/' + food.id)
      .then(() => {
        this.foodList = this.removeElemFromArray(food, this.foodList)
      }).catch(e => console.log(e))
    },
    removeElemFromArray(elem, array) {
      const index = array.indexOf(elem);
      if (index > -1) {
        array.splice(index, 1);
      }
      return array
    },
    
  },
  async created() {
    this.getAllFoodCategories()
    this.getNutrientStorageTypes()
    this.getAllFood()
  }
}
</script>