<template>
  <v-container class="pb-3">
    <v-data-table
      :headers="headers"
      :items="meal.courses"
      class="elevation-1"
      dense
      hide-default-footer
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>{{ mealName }}</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <p v-if="!isEditingEnabled">Calories: {{ meal.nutritionalValue.calories }}, proteins: {{ meal.nutritionalValue.proteinsInGrams }}, fats: {{ meal.nutritionalValue.fatsInGrams }}, carbs: {{ meal.nutritionalValue.carbsInGrams }}</p>
          <v-spacer></v-spacer>
          <v-btn v-if="isEditingEnabled"
            color="pink white--text"
            class="mb-2 mx-2"
            @click="emitDeleteMealEvent"
            >
              Delete meal
          </v-btn>
          <v-dialog v-if="isEditingEnabled"
            v-model="dialog"
            max-width="600px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >
                Add course
              </v-btn>
            </template>            
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>
              <v-form ref="form">
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col
                      cols="12"
                        md="6"
                      >
                        <v-select
                          v-model="editedItem.foodId"
                          :items="foodList"
                          item-value="id"
                          item-title="name"
                          item-text="name"
                          label="Food"
                          return-id
                          required
                          :rules="isNotEmptyRule">
                      </v-select>
                      </v-col>
                      <v-col
                        cols="12"
                        md="6"
                      >
                        <v-text-field
                          v-model="editedItem.quantity"
                          label="Quantity"
                          :rules="isNotNegativeNumberRule"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
              </v-form>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancel
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon v-if="isEditingEnabled"
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon v-if="isEditingEnabled"
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:[`item.name`]="{ item }">
        <p>{{ findFoodById(item.foodId).name }}</p>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Vue from 'vue'

export default {
  props: ['mealProp', 'isEditingEnabled', 'mealTypes','foodList'],
  data: () => ({
    meal: null,
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: 'Food',
        align: 'start',
        sortable: false,
        value: 'name',
      },
      { text: 'Quantiy (g)', value: 'quantity' },
      { text: 'Calories', value: 'nutritionalValue.calories' },
      { text: 'Fat (g)', value: 'nutritionalValue.fatsInGrams' },
      { text: 'Carbs (g)', value: 'nutritionalValue.carbsInGrams' },
      { text: 'Protein (g)', value: 'nutritionalValue.proteinsInGrams' },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    editedIndex: -1,
    editedItem: {
      foodId: null,
      name: '',
      quantity: 0,
    },
    defaultItem: {
      foodId: null,
      name: '',
      quantity: 0,
    },
    isNotEmptyRule: [str => !(str == null || str == undefined || str == '') || "The field is required"],
    isNotNegativeNumberRule: [str => (Number(str) !== Infinity && Number(str) >= 0) || "Should be not a negative number"],
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
    mealName() {
      if(this.meal == null) {
        return ''
      }
      return this.findMealTypeById(this.meal.mealTypeId).name
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },
  methods: {
    editItem (item) {
      this.editedIndex = this.meal.courses.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.meal.courses.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.meal.courses.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    save () {
      if(this.$refs.form.validate()) {
        let food = this.findFoodById(this.editedItem.foodId)
        this.editedItem.name = food.name

        if (this.editedIndex > -1) {
          Object.assign(this.meal.courses[this.editedIndex], this.editedItem)
        } else {
          this.meal.courses.push(this.editedItem)
        }
        this.close()
      }
    },
    findFoodById:function(foodId) {
      let food = this.foodList.find(elem => elem.id == foodId)
      return food
    },
    findMealTypeById:function(mealTypeId) {
      let result = this.mealTypes.find(elem => elem.id == mealTypeId)
      return result
    },
    emitDeleteMealEvent:function() {
      this.$emit('deleteMeal', this.meal)
    }
  },
  created() {
    this.meal = Vue.util.extend({}, this.mealProp)
  }
}
</script>