import Vue from 'vue'
import VueRouter from 'vue-router'
import FoodDictionary from './views/FoodDictionary.vue'
import DietDiary from './views/DietDiary.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'food',
    component: FoodDictionary
  },
  {
    path: '/food',
    name: 'food',
    component: FoodDictionary
  },
  {
    path: '/diary',
    name: 'dietDiary',
    component: DietDiary
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: '/',
  routes
})

export default router
