<template>
<!--        -->
  <div class="text-center">
    <v-dialog max-width="600">
      <template v-slot:activator="{ on }">
        <v-btn dark class="success" v-on="on">
          Add food
        </v-btn>
      </template>
      <v-card class="pa-4">
        <v-card-title primary-title>
          <h2>Add food</h2>
        </v-card-title>
        <v-card-text>
          <v-form class="px-3 pt-8" ref="form">
            <v-container>
              <v-layout row wrap justify-space-around align-center
                    v-for="(header,key) in headers" :key="key">
                <v-flex xs12 md4>
                  <p class="text-h6">{{ header.text }}</p>
                </v-flex>
                <v-flex xs12 md8>
                  <v-select v-if="header.type == 'foodCategorySelect'"
                    outlined
                    v-model="food.foodCategoryId"
                    :items="foodCategories"
                    item-value="id"
                    item-title="name"
                    item-text="name"
                    return-id
                    required
                    :rules = header.rule>
                  </v-select>
                  <v-select v-else-if="header.type == 'nutrientsStorageTypeSelect'"
                      outlined
                      v-model="food.nutrientsStorageTypeId"
                      :items="nutrientStorageTypes"
                      item-value="id"
                      item-title="name"
                      item-text="name"
                      return-id
                      required
                      :rules = header.rule>
                  </v-select>
                  <v-text-field v-else clearable outlined
                    v-model="food[header.value]"
                    single-line
                    :rules = header.rule
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap justify-space-around>
                <v-flex xs12 md8>
                  <v-btn text block size="x-large" class="success mt-3 pa-3" @click="submitForm">Add</v-btn>
                </v-flex>
              </v-layout>
            </v-container>         
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: ['foodCategories', 'nutrientStorageTypes'],
  data() {
    return {
      food: {},
      headers: [
        {
          text: "Name",
          align: "left",
          sortable: true,
          value: "name",
          type: "text",
          rule: [this.isNotEmpty || "Shouldn't be empty"],
        },
        { text: "Calories", value: "calories", type: "text", rule: [ this.isNotNegativeNumber || 'Should be not negative number'] },
        { text: "Fat (g)", value: "fatsInGrams", type: "text", rule: [ this.isNotNegativeNumber || 'Should be not negative number'] },
        { text: "Carbs (g)", value: "carbsInGrams", type: "text", rule: [ this.isNotNegativeNumber || 'Should be not negative number'] },
        { text: "Protein (g)", value: "proteinsInGrams", type: "text", rule: [ this.isNotNegativeNumber || 'Should be not negative number'] },
        { text: "Food Type", value: "foodCategoryId", type: "foodCategorySelect", rule: [ this.isNotNegativeNumber || 'Should be not negative number'] },
        { text: "Data per", value: "nutrientsStorageTypeId", type: "nutrientsStorageTypeSelect", rule: [ this.isNotNegativeNumber || 'Should be not negative number'] },
      ],
    }
  },
  methods: {
    isNotNegativeNumber:function(str) {
      var n = Number(str);
      return n !== Infinity && n >= 0;
    },
    isNotEmpty:function(str) {
      if(str == null || str == undefined || str == '')
        return false
      
      return true
    },
    submitForm:function() {
      if(this.$refs.form.validate()) {
        this.postFood(this.food)
      }
    },
    postFood:function(food) {
      this.$http.post(this.$hostname + '/api/v1/food', food)
        .then(this.$router.go())
        .catch(e => console.log(e))
    },
  },
}
</script>